import {Pipe, PipeTransform} from '@angular/core';
@Pipe({ name: 'roundNum' })
export class RoundPipe implements PipeTransform {
    transform(value: number, addTo: number): number {
        // return Math.round(value);
        if(addTo !== undefined){
            return value + addTo;
        }
        return value + 10;
    }
}



