import { Component, OnInit } from '@angular/core';
import {WeatherService} from './weather.service';
import {Wheather} from './weather';
import { error } from 'protractor';

@Component({
  selector: 'app-weather-call-api',
  templateUrl: './weather-call-api.component.html',
  styleUrls: ['./weather-call-api.component.css'],
  providers: [WeatherService]
})
export class WeatherCallApiComponent implements OnInit {
  constructor(private weatherService: WeatherService) { }
  txtCityName = '';
  cityName = '';
  temp = null;
  ngOnInit() {}
  isLoading =  false;

  getWeather() {
    this.isLoading = true;

    this.weatherService.getTemp(this.txtCityName.replace(/\s/g, ""))
    .subscribe(
      x => {
      this.cityName = this.txtCityName;
      this.temp = x != null ? x['main'] : null;
      this.isLoading = false;
    },
    err => {
      alert('no data for your city.');
      this.isLoading = false;
    },
    () => console.log('done loading weather data')
    );  
  }

  showMessage(){
    if(this.isLoading){
      return 'Loading ...';
    }
    else{
      return this.cityName === '' ? 'Enter your city name' : this.temp != null ? (this.cityName + ' is now ' + this.temp['temp']) : 'Sorry! no data for ' + this.cityName +' city';
    }
  }

}



